.chatPage{
    background-color: rgb(24, 24, 24);
    width: 100vw;
    height: 100vh; 
    display: flex;
    justify-content: center;
    justify-content: center;
    align-items: center; 
    align-items: center;
    align-items: center;  
}
.chatContainer{
    background-color: white;
    height: 60%;
    width: 50%;
    
}
.header{
    background-color: orange;
    height: 15%;
    height: 15%;
}
.chatBox{
    border: 0.2vmax solid black;
    height: 70%;
    box-sizing: border-box;
    overflow-y: hidden;
}
.inputBox{
    border: 0.2vmax solid black;
    height: 15%;
    box-sizing: border-box;
    display: flex;
}
#chatInput{
    width:80%;
    border: none;
    background-color: white;
    box-sizing: border-box;
    padding: 1.6vmax;
    outline:none;

}
.sendBtn{
    background-color: orange;
    color:white;
    border: none;
    width: 21%;
    transition: all 0.3s;
    cursor: pointer;
    font-size: 1.2vmax;


}
.sendBtn:hover{
    background-color: orange;
}

.sendBtn>img{
    width:5vmax;
    /* filter:brightness(100) invert(); */
    transition: all 0.3s;
}
.sendBtn:hover img{
    transform: translateX(10px);
}

