@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@1,400;1,500;1,600&display=swap');

body{
  margin: 0;
  padding: 0;
  /* font-family: 'Lato', sans-serif; */
  font-family: 'Playfair Display', serif;
}
@media(max-aspect-ratio:7/10){
  .JoinPage{
    display: flex;
    justify-content: center;
    align-items: flex-start !important;

  }
  .JoinContainer{
    /* border: 2px solid white; */
    /* height: 50%; */
    margin-top: 70vw;
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center; 
}
.JoinContainer > h1{
    color:white;
    font-size: 2.5vmax;
    width: 25vmax;
    border-bottom: 0.2vmax solid white;
    text-align: center;
    padding: 1vmax;
    font-family: 'Playfair Display';
}
.JoinContainer>img{
    width:10vmax; 
}
.chatContainer{
  background-color: white;
  height: 100% ;
  width: 100%;
}
.chatPage{
  margin-top: 10vw;
}
.header{
  height:8%;
}
.inputBox{
  height: 10%;
}
}