
.JoinPage{
    background-color: rgb(24, 24, 24);
    width: 100vw;
    height: 100vh; 
    display: flex;
    justify-content: center;
    align-items: center; 
}
.JoinContainer{
    /* border: 2px solid white; */
    /* height: 50%; */
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center; 
}
.JoinContainer > h1{
    color:white;
    font-size: 2.5vmax;
    width: 25vmax;
    border-bottom: 0.2vmax solid white;
    text-align: center;
    padding: 1vmax;
    font-family: 'Playfair Display';
}
.JoinContainer>img{
    width:10vmax; 
}
#joinInput{
    width: 25vmax;
    padding: 1.5vmax;
    border: none;
    outline: none;
    font-size: 1.2vmax;
    margin: 2vmax;
    font-family: 'Playfair Display';

}
.joinbtn{
    width:28vmax;
    padding: 1.5vmax;
    border: none;
    outline: none;
    font-size: 1.2vmax;
    background-color:darkorange;
    box-sizing: border-box;
     color:white;
      transition: all 0.3s;
      cursor: pointer ;
      font-family: 'Playfair Display';
       

}
.joinbtn:hover{
    background-color: darkorange  ;
}

